export default angular.module('deskworks.system.AccessCardEditCtrl', [
  'deskworks.accessCard.service',
])

  .controller('AccessCardEditCtrl', function($scope, $translate, dwAlerts, state, helper, user,
                                                   accessCard, AccessCard) {
    const vm = this;

    vm.accessCard = accessCard || {};

    state.setPageTitle($translate.instant(
      vm.accessCard.id ? 'ACCESS_CARDS.EDIT.PAGE_TITLE' : 'ACCESS_CARDS.NEW.PAGE_TITLE',
      { userName: vm.accessCard.id ? vm.accessCard.userName : user.firstName + ' ' + user.lastName }
    ));

    /**
     * Save Access Card handler
     */
    vm.save = () => {
      if (helper.pointFirstInvalid('#accessCard')) return;

      const data = {
        number: vm.accessCard.number,
        notes: vm.accessCard.notes,
      };

      if (!vm.accessCard.id) data.userId = state.getCurrentUserId();

      const promise = vm.accessCard.id ?
        AccessCard.update({ centerId: state.getCurrentCenterId(), accessCardId: vm.accessCard.id }, data).$promise :
        AccessCard.save({ centerId: state.getCurrentCenterId() }, data).$promise;

      return promise
        .catch(err => dwAlerts.error(err, 'Failed to save access card.'))
        .then(result => {
          if (vm.accessCard.id) {
            vm.form.$setPristine();
            return result;
          } else {
            return state.setPath('/access-cards/' + result.id);
          }
        });
    };

    /**
     * Delete Access Card handler
     */
    vm.delete = accessCard => {
      if (!confirm($translate.instant('ACCESS_CARDS.CONFIRM_DELETE'))) return;

      return AccessCard.delete({ centerId: state.getCurrentCenterId(), accessCardId: vm.accessCard.id }).$promise
        .catch(err => dwAlerts.error(err, 'Failed to delete access card.'))
        .then(() => state.setPath('/access-cards'));
    };

    /**
     * Back button handler
     */
    vm.back = () => state.setPath('/access-cards');

    $scope.$watch('state.getCurrentCenterId()', (nv, ov) => { if (nv !== ov) vm.back() });
  });
