export default angular.module('deskworks.LeadSourceListCtrl', [
  'deskworks.system.service',
  'deskworks.routeHelper',
])

  .controller('LeadSourceListCtrl', function($scope, $translate, dwAlerts, state, helper, leadSources,
                                             systemService) {
    const vm = this;

    state.setPageTitle($translate.instant('LEAD_SOURCES.PAGE_TITLE'));

    vm.leadSources = leadSources;

    /**
     * Update Lead Source handler
     */
    vm.update = (leadSource, form, index) => {
      if (helper.pointFirstInvalid('#leadSource' + index)) return;

      const data = {
        position: leadSource.position,
        name: leadSource.name,
      };

      const promise = leadSource.id ? systemService.updateLeadSource(leadSource.id, data) : systemService.createLeadSource(data);
      return promise
        .catch(err => dwAlerts.error(err, 'Failed to save lead source.'))
        .then(result => {
          form.$setPristine();
          leadSource.id = result.id;
          sortLeadSources();
        });
    };

    /**
     * New LeadSource handler
     */
    vm.new = () => vm.leadSources.push({});

    /**
     * Delete LeadSource handler
     */
    vm.delete = (leadSource, index) => {
      if (leadSource.id) { // existing Lead Source
        return systemService.removeLeadSource(leadSource.id)
          .catch(err => dwAlerts.error(err, 'Failed to save lead source.'))
          .then(() => { vm.leadSources.splice(index, 1); });
      } else { // new Lead Source
        vm.leadSources.splice(index, 1);
      }
    };

    /**
     * Sorts vm.leadSources by position. Called by vm.update method
     */
    function sortLeadSources() {
      vm.leadSources.sort((leadSource1, leadSource2) => {
        if (leadSource1.position > leadSource2.position) return 1;
        if (leadSource1.position < leadSource2.position) return -1;
        return 0;
      });
    }
  });
