export default angular.module('deskworks.AdministratorListCtrl', [])

  .controller('AdministratorListCtrl', function($scope, $translate, state, helper, admins) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('ADMINISTRATORS.PAGE_TITLE'));

    $ctrl.admins = admins;

    /**
     * Edit Admin handler
     */
    $ctrl.edit = admin => state.setPath('/administrators/' + admin.id);

    /**
     * New Admin handler
     */
    $ctrl.new = () => state.setPath('/administrators/new');
  });
