import messageBoardUserSettingsDirective from 'shared/message-board-user-settings/message-board-user-settings.directive';
import dwEmailDirective from 'shared/dw-controls/dw-email.directive';
import dwLoginDirective from 'shared/dw-controls/dw-login.directive';
import dwPasswordDirective from 'shared/dw-controls/dw-password.directive';
import dwLastNameDirective from 'shared/dw-controls/dw-last-name.directive';
import dwPhoneDirective from 'shared/dw-controls/dw-phone.directive';

export default angular.module('deskworks.AdminEditCtrl', [
  'deskworks.Admin',
  'deskworks.center.service',
  'deskworks.helper',
  'deskworks.optsAdminPermission',
  'deskworks.adminRolesMatrix',
  messageBoardUserSettingsDirective.name,
  dwEmailDirective.name,
  dwLoginDirective.name,
  dwPasswordDirective.name,
  dwLastNameDirective.name,
  dwPhoneDirective.name,
])

  .controller('AdminEditCtrl', function($scope, $window, $translate, $rootScope, $route, $q, state, helper,
                                        dwAlerts, admin, userProfile, adminRolesMatrix,
                                        Admin, upPrinterCodeService, upPhotoService, optsAdminPermission) {
    const $ctrl = this;

    state.setPageTitle($translate.instant(admin.id ? 'ADMINISTRATORS.EDIT.PAGE_TITLE' : 'ADMINISTRATORS.NEW.PAGE_TITLE'));

    $ctrl.isNew = $route.current.params.adminId === 'new';
    $ctrl.admin = admin;
    $ctrl.roles = admin.options.roles
      .filter(role => adminRolesMatrix[state.getLoggedUser().role].indexOf(role.id) >= 0 || $ctrl.admin.role.id === role.id);
    $ctrl.admin.printerCode = angular.copy(userProfile.printerCodes && userProfile.printerCodes[0] || { id: helper.uid(), code: '' });
    $ctrl.userProfile = userProfile;
    $ctrl.schema = { photo: true };
    $ctrl.userProfile.isNew = () => !$ctrl.admin.id;
    $ctrl.optsPermission = optsAdminPermission;

    /**
     * Save Admin handler
     */
    $ctrl.save = () => {
      if (helper.pointFirstInvalid('#administrator')) return;

      return Admin.save($ctrl.admin)
        .catch(err => dwAlerts.error(err, 'Failed to save administrator.'))
        .then(result => {
          $ctrl.userProfile.id = result.id;
          const promises = [];

          if ($ctrl.admin.printerCode.code !== ($ctrl.userProfile.printerCodes && $ctrl.userProfile.printerCodes[0] || { code: '' }).code) {
            if ($ctrl.admin.printerCode.code) {
              promises.push(
                upPrinterCodeService.save(state.getCurrentCenterId(), $ctrl.userProfile.id, $ctrl.admin.printerCode)
                  .catch(err => dwAlerts.error(err, 'Failed to save printer code.'))
                  .then(() => { $ctrl.userProfile.printerCodes = [ $ctrl.admin.printerCode ]; })
              );
            } else { // blank printer code, delete it
              promises.push(
                upPrinterCodeService.delete(state.getCurrentCenterId(), $ctrl.userProfile.id, $ctrl.admin.printerCode)
                  .catch(err => dwAlerts.error(err, 'Failed to delete printer code.'))
                  .then(() => { $ctrl.userProfile.printerCodes = []; })
              );
            }
          }

          if ($ctrl.isNew) {
            promises.push(
              upPhotoService.save(state.getCurrentCenterId(), $ctrl.userProfile)
                .catch(err => dwAlerts.error(err, 'Failed to save photo.'))
            );
          }

          if ($ctrl.saveMessageBoardUserSettings) {
            promises.push(
              $ctrl.saveMessageBoardUserSettings()
                .catch(err => dwAlerts.error(err, 'Failed to save message board user settings.'))
            );
          }

          return $q.all(promises);
        })
        .then(() => {
          $rootScope.$emit('CENTERS:REFRESH');
          if (!$ctrl.isNew) return $ctrl.form.$setPristine();
          return state.setPath(`/administrators/${$ctrl.admin.id}`);
        });
    };

    /**
     * Remove Admin handler
     */
    $ctrl.remove = () => {
      if (!$window.confirm($translate.instant('ADMINISTRATORS.CONFIRM_DELETE'))) return;

      return Admin.delete($ctrl.admin.id)
        .catch(err => dwAlerts.error(err, 'Failed to delete administrator.'))
        .then(() => state.setPath('/administrators'));
    };

    /**
     * Back button handler
     */
    $ctrl.back = () => state.setPath('/administrators');

    /**
     * Navigate Tasks
     */
    $ctrl.toTasks = $event => {
      $event.preventDefault();
      return state.setPath('/tasks');
    };
  });
