import reportHelper from 'shared/reports/services/report-helper.service';

export default angular.module('deskworks.AccessCardListCtrl', [
  'deskworks.system.service',
  'deskworks.accessCard.service',
  reportHelper.name,
])

  .controller('AccessCardListCtrl', function($scope, $route, $timeout, $translate, dwAlerts, state, helper,
                                             accessCards, center, AccessCard, reportHelper) {
    const vm = this;

    state.setPageTitle($translate.instant('ACCESS_CARDS.PAGE_TITLE'));

    vm.accessCards = accessCards;

    /**
     * Edit Access Card handler
     */
    vm.edit = accessCard => state.setPath('/access-cards/' + accessCard.id);

    /**
     * New Access Card handler
     */
    vm.new = () => state.setPath('/access-cards/new');

    /**
     * Delete Access Card handler
     */
    vm.delete = accessCard => {
      if (!confirm($translate.instant('ACCESS_CARDS.CONFIRM_DELETE'))) return;

      return AccessCard.delete({ centerId: state.getCurrentCenterId(), accessCardId: accessCard.id }).$promise
        .catch(err => dwAlerts.error(err, 'Failed to delete access card.'))
        .then(() => {
          $route.reload();
          return $timeout(6000);
        });
    };

    /**
     * Download CSV handler
     */
    vm.download = () => {
      const config = { centerId: center.id, reportName: state.getPageTitle() };
      const resources = { centers: [center] };
      const fileName = reportHelper.getReportFileName({ extension: 'csv', config, resources });
      const report = {
        columns: [
          { title: $translate.instant('ACCESS_CARDS.USER') },
          { title: $translate.instant('ACCESS_CARDS.NUMBER') },
          { title: $translate.instant('ACCESS_CARDS.NOTES') },
        ],
        rows: vm.accessCards.map(card => [card.userName, card.number, card.notes]),
      };

      const csv = reportHelper.buildCsv({ report });
      reportHelper.download({ blob: csv, fileName });
    };
  });
